import setBettingCriteria from '@/services/simulator/breakeven/set-betting-criteria-for-calling';

self.addEventListener('message', function (e) {
  if (e.data.message === 'set equity for each betting criterion') {
    let bettingCriteriaOnePercentIncrementsContainer = { value: 0 };
    let setEquityForBettingCriterionFunctions = [];

    for (let valueBetToBluffRatio in e.data.bettingCriteria[e.data.streetToCall]) {
      e.data.bettingCriteria[e.data.streetToCall][valueBetToBluffRatio].forEach(function (bettingCriterion) {
        if (bettingCriterion.weightedRange) {
          setEquityForBettingCriterionFunctions.push(function () {
            return setBettingCriteria.setEquityForBettingCriterion(
              bettingCriterion,
              e.data.presetCommunityCardsArray,
              e.data.heroWeightedRange,
              e.data.numberOfSimulations,
              e.data.rakePercentage,
              e.data.tipPercentage,
              bettingCriteriaOnePercentIncrementsContainer,
              function (bettingCriteriaOnePercentIncrementsContainer) {
                bettingCriteriaOnePercentIncrementsContainer.value++;

                if (bettingCriteriaOnePercentIncrementsContainer.value % (e.data.totalNumOnePercentIncrements / 100) === 0) {
                  self.postMessage({
                    message: 'progress',
                    value: ((bettingCriteriaOnePercentIncrementsContainer.value / e.data.totalNumOnePercentIncrements) * 100),
                    bettingCriteria: e.data.bettingCriteria,
                  });
                }
              }
            );
          });
        }
      }, this);
    }

    setEquityForBettingCriterionFunctions.reduce(function (prev, curr) {
      return prev.then(curr);
    }, Promise.resolve()).then(function () {
      self.postMessage({
        message: 'finished',
        bettingCriteria: e.data.bettingCriteria,
      });
    });
  }
});



